import React from "react";
import { Popover, Button } from "antd";
import { DownOutlined } from "@ant-design/icons";
import {
  DOWNLOAD,
  DOWNLOAD_AS_CSV,
  DOWNLOAD_AS_PDF,
  SHOW_AS_BAR,
  SHOW_AS_PIE,
} from "../../headings";

const content = (props) => (
  <div>
    {props.chartOptions ? (
      <ul>
        <li>
          <Button onClick={() => props.showChartOptions(!props.chartType)}>
            {!props.chartType ? SHOW_AS_PIE : SHOW_AS_BAR}
          </Button>
        </li>
      </ul>
    ) : (
      <ul>
        <li>
          <Button onClick={() => props.showDownloadOptions("pdf")}>
            {DOWNLOAD_AS_PDF}
          </Button>
        </li>
        <li>
          <Button onClick={() => props.showDownloadOptions("csv")}>
            {DOWNLOAD_AS_CSV}
          </Button>
        </li>
      </ul>
    )}
  </div>
);

class PollResponsePopover extends React.Component {
  state = {
    visible: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props !== nextProps && this.state.visible) {
      this.hide();
    }
  }

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          {this.props.chartOptions ? (
            <Button type="primary">
              {this.props.chartType ? SHOW_AS_PIE : SHOW_AS_BAR}
            </Button>
          ) : (
            <Button type="primary">{DOWNLOAD}</Button>
          )}
          <DownOutlined className="down-arrow-ico white" />
        </Popover>
      </>
    );
  }
}

export default PollResponsePopover;
